import { ChevronRight } from "lucide-react";
import { useRouter } from "next/navigation";

export default function WeeklyPlan() {
  const router = useRouter();

  return (
    <div className="bg-white rounded-2xl shadow-sm p-5 mb-6 relative">

      <div
        className="absolute right-4 top-4 cursor-pointer text-gray-400"
        onClick={() => router.push("/meal-plan")}
      >
        <ChevronRight size={22} />
      </div>

      <h3 className="font-semibold text-lg">
        Sua sugestão personalizada
      </h3>

      <p className="text-gray-600 mt-2">
        Visualize o plano semanal gerado pela IA — toque no dia para ver detalhes.
      </p>
    </div>
  );
}
